.inner {
    background-color: #ffffff;
    padding: 30px 0;
    box-sizing: border-box;
    margin-bottom: 20px;
}

.title {
    margin: 0;
    font-size: 20px;
    position: relative;
    padding: 0 30px 14px 30px;
}

.title::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #909ba3;
    left: 0;
    bottom: 0;
}

.text {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    padding: 0 30px;
}